.scrollTop {
    position: fixed; 
    width: 190%;
    bottom: 20px;
    align-items: right;
    height: 20px;
    justify-content: right;
    z-index: 0;
    cursor: pointer;
    animation: fadeIn 0.3s;
    opacity: 0.7;
  }
  
  .scrollTop:hover{
    opacity: 1;
  }
  
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }