.social-items {
    position: absolute;
    padding: 0;
    display: flex;
}

.social-items li {
    list-style: none;
}

.social-items li a {
    position: relative;
    width: 60px;
    height: 60px;
    display: block;
    text-align: center;
    margin: 0 10px;
    border-radius: 50%;
    padding: 6px;
    box-sizing: border-box;
    text-decoration: none;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);
    background: linear-gradient(0deg, #ddd, #fff);
    transition: .5s;
}

.social-items li a:hover{
    box-shadow: 0 2px 5px rgba(0,0,0,0.3);
    text-decoration: none; 
}

.social-items li a:hover .fa {
    color: #3b5998;
}

.social-items li a .fa {
    width: 100%;
    height: 100%;
    display: block;
    background: linear-gradient(0deg, #fff, #ddd);
    border-radius: 50%;
    line-height: calc(60px - 12px);
    font-size: 24px;
}

.copyrights-text{
    margin-bottom: -35px;
}

.footer-logo img{ 
    width: 350px;
}


@media screen and (max-width: 370px){
    .social-items li i{
        margin-left: -50px;
    }

    .footer-logo img{
        margin-left: -40px;
    }
}
@media screen and (max-width: 720px){
    .social-items li i{
        margin:auto;
    }

    .footer-logo img{
        margin-left: -60px;
    }
}

@media screen and (max-width: 320px){
    .social-items li i{
        margin-left: -80px;
    }

    .footer-logo img{
        margin-left: -40px;
    }
}
@media screen and (min-width: 250px){
    .social-items li i{
        margin-left: -80px;
    }

    .footer-logo img{
        margin-left: -60px;
    }
}